import React, { Component } from "react";
import {
  Tabs,
  Tab,
  Card,
  CardTitle,
  CardActions,
  CardText,
  Button,
  Grid,
  Cell
} from "react-mdl";

export default class Projects extends Component {
  constructor(props) {
    super(props);
    this.state = { activeTab: 0 };
  }

  getReactJSProjects() {
    var el = [1, 2, 3, 4].map(() => (
      <Cell col={4} phone={12}>
        <Card
          shadow={0}
          style={{ "min-width": "320px", height: "320px", margin: "auto" }}
        >
          <CardTitle
            expand
            style={{
              color: "#fff",
              background:
                "url(http://www.getmdl.io/assets/demos/dog.png) bottom right 15% no-repeat #46B6AC"
            }}
          >
            Update
          </CardTitle>
          <CardText>
            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aenan
            convallis.
          </CardText>
          <CardActions border>
            <Button colored>Live Demo</Button>
            <Button colored>Github Repo</Button>
          </CardActions>
        </Card>
      </Cell>
    ));

    return <Grid>{el}</Grid>;
  }
  getNodeJSProject() {
    var el = [1, 2, 3].map(() => (
      <Cell col={4} phone={12}>
        <Card
          shadow={0}
          style={{ "min-width": "320px", height: "320px", margin: "auto" }}
        >
          <CardTitle
            expand
            style={{
              color: "#fff",
              background:
                "url(http://www.getmdl.io/assets/demos/dog.png) bottom right 15% no-repeat #46B6AC"
            }}
          >
            Update
          </CardTitle>
          <CardText>
            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aenan
            convallis.
          </CardText>
          <CardActions border>
            <Button colored>Live Demo</Button>
            <Button colored>Github Repo</Button>
          </CardActions>
        </Card>
      </Cell>
    ));

    return <Grid>{el}</Grid>;
  }
  getAllOtherProject() {
    var el = [1, 2, 3, 4, 5, 6, 7, 8].map(() => (
      <Cell col={4} phone={12}>
        <Card
          shadow={0}
          style={{ "min-width": "320px", height: "320px", margin: "auto" }}
        >
          <CardTitle
            expand
            style={{
              color: "#fff",
              background:
                "url(http://www.getmdl.io/assets/demos/dog.png) bottom right 15% no-repeat #46B6AC"
            }}
          >
            Update
          </CardTitle>
          <CardText>
            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aenan
            convallis.
          </CardText>
          <CardActions border>
            <Button colored>Live Demo</Button>
            <Button colored>Github Repo</Button>
          </CardActions>
        </Card>
      </Cell>
    ));

    return <Grid>{el}</Grid>;
  }
  getProjects(index) {
    switch (index) {
      case 0:
        return this.getReactJSProjects();
      case 1:
        return this.getNodeJSProject();
      case 2:
        return this.getAllOtherProject();
      default:
        break;
    }
    return <h1>Hello</h1>;
  }
  render() {
    return (
      <div className="category-tabs">
        <Tabs
          activeTab={this.state.activeTab}
          onChange={tabId => this.setState({ activeTab: tabId })}
          ripple
        >
          <Tab>ReactJS</Tab>
          <Tab>Android</Tab>
          <Tab>Others</Tab>
        </Tabs>
        <section>
          <div className="category-grid">
            {this.getProjects(this.state.activeTab)}
          </div>
        </section>
      </div>
    );
  }
}
